import { config } from './main'

const filename = window.location.pathname.substring(window.location.pathname.lastIndexOf('/') + 1)

// =====================================================================================
// Good old fashion math clamping function
// =====================================================================================
export function clamp(val, min, max) {
  return Math.min(Math.max(val, min), max)
}

// =====================================================================================
// Formats labels for widgets with dynamic values based on formatting codes
// =====================================================================================
export function formatLabel(input, value = '', percent = '', chan = '', cc = '', note = '') {
  let newLabel = input.replaceAll('\\n', '<br>')
  // This handles %v and also %v+mod and %v-mod, e.g. '%v-63' to subtract 63 from the value
  newLabel = newLabel.replaceAll(/%v([-+]\d+)?/g, (match, modifier) => {
    if (modifier) {
      return value + parseInt(modifier)
    }
    return value
  })
  newLabel = newLabel.replaceAll('%h', chan)
  newLabel = newLabel.replaceAll('%t', cc)
  newLabel = newLabel.replaceAll('%n', note)
  newLabel = newLabel.replaceAll('%a', midiNoteName(note))
  newLabel = newLabel.replaceAll('%p', percent)
  newLabel = newLabel.replaceAll('%%', '%')
  return newLabel
}

// =====================================================================================
// Helper to darken colours, actually it makes them semi-opaque
// =====================================================================================
export function darkenColour(colour = '', hexOpacity = '60') {
  if (colour.startsWith('#')) {
    return `${colour}${hexOpacity}`
  } else {
    return `${colourNameToHex(colour)}${hexOpacity}`
  }
}

// =============================================================================
// Save widget value to local storage
// =============================================================================
export function saveWidgetValue(widgetName, id, value) {
  // IMPORTANT: This prevents all widgets saving their zero values when first starting
  if (value === Number.MIN_SAFE_INTEGER) return
  // Don't save anything when save/restored is disabled
  if (!config.restoreValues) return

  //console.log('SETTING', `touchmidi.${filename}.${widgetName}.${id}`, value)
  localStorage.setItem(`touchmidi.${filename}.${widgetName}.${id}`, value)
}

// =============================================================================
// Restore widget value from local storage
// =============================================================================
export function getWidgetValue(widget, id = '') {
  if (!id) id = `${widget.cc}${widget.chan}${widget.nrpn}`
  const widgetName = widget.tagName.toLowerCase()

  //console.log('GETTING', `touchmidi.${filename}.${widgetName}.${id}`)
  return localStorage.getItem(`touchmidi.${filename}.${widgetName}.${id}`)
}

// =============================================================================
// Remove all values for this layout file
// =============================================================================
export function removeStorage() {
  // eslint-disable-next-line no-unused-vars
  for (let [key, value] of Object.entries(localStorage)) {
    if (key.startsWith(`touchmidi.${filename}`)) {
      localStorage.removeItem(key)
    }
  }
}

// =============================================================================
// Convert a MIDI note number to a named string, e.g. 49 = 'C♯4'
// =============================================================================
export function midiNoteName(midiNote) {
  let noteNames = ['C', 'C&sharp;', 'D', 'D&sharp;', 'E', 'F', 'F&sharp;', 'G', 'G&sharp;', 'A', 'A&sharp;', 'B']
  return noteNames[midiNote % 12] + (Math.floor(midiNote / 12) - 1)
}

