import { parseCSV } from './setlist/csv'

export async function loadSetList(url) {
  const response = await fetch(url)
  const csvText = await response.text()
  let csvArray = parseCSV(csvText)
  let counter = 0
  let rowCount = 0
  let rowDiv = document.createElement('group-row')
  document.body.appendChild(rowDiv)
  //['Patch Name', 'XP50 User', 'JX8P', 'Korg', 'XP50 Perform', 'AKAI']
  csvArray.shift()
  csvArray = csvArray.sort((a, b) => {
    return a[0] > b[0] ? 1 : -1
  })
  for (let row of csvArray) {
    console.log(counter, row)
    const patchName = row[0]
    const xp50User = `1,1,1,${row[1] - 1}`
    const jx8P = `2,0,1,${row[2]-1}`
    const korg = `3,0,1,${row[3]}`
    const xpperform = row[4] //TODO
    const akai = row[5] //TODO
    const el = document.createElement('midi-button')
    el.setAttribute('label', patchName)
    el.setAttribute('multiprog', `${xp50User}|${jx8P}|${korg}`)
    if(xpperform.length>0){
      console.log('xpperform', xpperform)
      el.setAttribute('xpperform', xpperform)
    }
    el.setAttribute('hsl', ((counter / (csvArray.length - 2)) * 360).toString())
    rowDiv.appendChild(el)
    console.log(patchName, xp50User, jx8P, korg, xpperform, akai)
    if (rowCount > 5) {
      rowDiv = document.createElement('group-row')
      document.body.appendChild(rowDiv)
      rowCount = 0
    }
    rowCount++;
    counter++;
  }

}

export async function fetchSetList() {
  await loadSetList(`../assets/csv/patches.csv?random=${Math.random()}`);
}
