export default "@import url('https://fonts.googleapis.com/css2?family=PT+Sans&display=swap');\r\n\r\n:root {\r\n  /* these variables are used everywhere */\r\n  --bg: black; /* background colour */\r\n  --b-radius: 0; /* border radius */\r\n  --b-width: 0rem; /* border thickness */\r\n  --spacing: 0rem; /* widget spacing */\r\n\r\n  --font: sans-serif; /* Consolas, 'Courier New', monospace; */\r\n}\r\n\r\nhtml,\r\nbody {\r\n  height: 100%;\r\n  margin: 0;\r\n  padding: 0;\r\n  overflow-y: hidden;\r\n  color: white;\r\n  background-color: var(--bg);\r\n  font-family: var(--font);\r\n  touch-action: none;\r\n  padding-right: 10px;\r\n}\r\n\r\nbody {\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n/* Used to hide the page behind the config dialog */\r\n#pageMask {\r\n  background: rgba(0, 0, 0, 0.8);\r\n  position: fixed;\r\n  top: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  left: 0;\r\n  display: none;\r\n}\r\n";