/*
  TouchMIDI v2
  button.js - A widget that sends MIDI events when pressed or toggled on/off
  Ben Coleman, Dec 2020
*/

import { html, define, property } from 'hybrids'
import * as midi from '../midi.js'
import { darkenColour, formatLabel } from '../utils.js'
import css from './button.css'

function eventReleased(host, evt) {
  evt.preventDefault()
  if (!host.toggle && host._pressed) {
    host._pressed = false

    if (host.note > 0) {
      midi.sendNoteOff(host.note, host.chan)
    }
    if (host.cc > 0 && host.valueOff) {
      midi.sendCC(host.cc, host.chan, host.valueOff)
    }
    if (host.nrpn && host.valueOff) {
      midi.sendNRPN(host.nrpn, host.chan, host.valueOff, host.nrpnHires)
    }
  }
}

function eventPressed(host, evt) {
  evt.preventDefault()
  if (host.toggle) {
    host._pressed = !host._pressed
  } else {
    host._pressed = true
  }

  // Handle MIDI actions, might be note, CC or NRPN
  if (host._pressed) {
    if (host.multinote.length >0){
      const notes = host.multinote.split(',');
      midi.sendProgChange('1,1', 2, 28, true)
      midi.sendCC(7,2, 64);
      for (const note of notes){
        midi.sendNoteOn(parseInt(note), parseInt(host.chan), host.velo);
      }
      setTimeout(()=>{
        for (const note of notes){
          midi.sendNoteOff(parseInt(note), parseInt(host.chan), 0);
        }
        midi.sendProgChange('1,1', 2, 31, true)
      },20000);
    }
    if (host.cc > 0) {
      midi.sendCC(host.cc, host.chan, host.value)
    }
    if (host.nrpn) {
      midi.sendNRPN(host.nrpn, host.chan, host.value, host.nrpnHires)
    }
    if (host.note > 0) {
      midi.sendNoteOn(host.note, host.chan, host.velo)
    }
    if (host.prog) {
      midi.sendProgChange(host.prog, host.chan, host.value)
    }
    if (host.multiprog) {
      console.log('MULTIPROG', host.multiprog);
    }
    if (host.xpperform && host.xpperform.length>0) {
      const bank = '1,1';
      let prog = parseInt(host.xpperform) - 1;
      console.log('xpperform', prog, 16);
      midi.sendProgChange(bank, 16, prog, true)
    }
    if (host.multiprog) {
      const programs = host.multiprog.split('|');
      for (const program of programs) {
        let progInfo = program.trim().split(',');
        if (progInfo.length !== 4) {
          throw new Error(`Malformed multiprog, should be "1,1,1,1|2,1,1,2|3,1,1,3" etc, is: ${host.multiprog}`);
        }
        const channel = parseInt(progInfo[0]);
        const bank = progInfo[1] + ',' + progInfo[2];
        let prog = parseInt(progInfo[3]);
        if (!isNaN(prog) && prog > -1){
          midi.sendProgChange(bank, channel, prog, true)
        }
      }

    }
  } else if (host.toggle) {
    if (host.note > 0) {
      midi.sendNoteOff(host.note, host.chan)
    }
    if (host.cc > 0 && host.valueOff) {
      midi.sendCC(host.cc, host.chan, host.valueOff)
    }
    if (host.nrpn && host.valueOff) {
      midi.sendNRPN(host.nrpn, host.chan, host.valueOff, host.nrpnHires)
    }
    if (host.prog && host.valueOff) {
      midi.sendProgChange(host.prog, host.chan, host.valueOff)
    }
  }
}

export const Component = {
  // Private internal properties
  _pressed: false,
  _width: 30,

  // MIDI properties
  chan: 1,
  cc: -1,
  nrpn: '',
  nrpnHires: false,
  value: 0,
  valueOff: 0,
  velo: 127,
  note: -1,
  prog: '',
  multiprog: '',
  multinote: '',
  xpperform: '',
  hsl: '',

  // Display & UX properties
  colour: property('#ffffff'),
  label: '__unset__',
  toggle: false,
  labelScale: 1,
  grow: 1,

  render: ({ label, colour, _pressed, chan, cc, nrpn, prog, note, _width, labelScale, grow, hsl }) => {

    // Default label is to show the value
    if (label === '__unset__') {
      if (note > 0) label = '%a'
      else if (cc > 0) label = '%t'
      else if (nrpn) label = nrpn
      else if (prog) label = prog
    }

    const newStyle = `
    :host {
      flex-grow: ${grow}
    }
    button {
      background-color: hsl(${hsl}, 100%, 20%);
      color: ${colour};
      border-color: ${colour};
    }`

    return html`<button
      onmouseup="${eventReleased}"
      ontouchend="${eventReleased}"
      onmousedown="${eventPressed}"
      ontouchstart="${eventPressed}"
      onmouseleave="${eventReleased}"
      innerHTML="${formatLabel(label, '', '', chan, cc, note)}"
    ></button>`.style(css, newStyle)
  }
}

define('midi-button', Component)
